<template>
  <div class="main-body">
    <firstnav />
    <div class="login-area">
      <div class="row">
        <div class="col-md-6 lg-6 sm-12" id="col-1">
          <!-- <h4>dfghjk</h4> -->
        </div>
        <div class="col-md-6 lg-6 sm-12" id="col-2">
          <div class="white-bg-area">
            <h4 id="col-2-h4"><b>{{ colTwoTitle }}</b></h4>
            <marquee><p>Kindly login if registered....................Kindly login if registered</p></marquee>
            <form>
              <div class="form-group">
                <input type="text" name="" placeholder="Enter Username / Email address" class="form-control">
              </div>
              <div class="form-group">
                <input type="password" name="" placeholder="Enter password" class="form-control">
              </div>
              <div class="form-group">
                <input type="submit" value="Login" class="btn btn-success" id="login-btn">
              </div>
            </form>
            <p>Forgot Password,<router-link to="/" id="router"> Click here</router-link></p>
            <p class="linker">I don't have an account, <router-link to="/register" id="router">Register Now</router-link></p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import firstnav from '../components/navbar/firstnav'

export default {
  components: {
    firstnav
  },
  data () {
    return {
      colTwoTitle: 'Login Your Account'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  /*.login-area {
    background-color: whitesmoke;
  }*/

  #col-2 {
    background-color: #fff;
    text-align: center;
  }

  .row {
    width: 100%;
    margin-right: 0%;
    margin-left: 0%;
    margin-top: 60px;
    background-image: url('../assets/login/pic03.jpg');
    background-size: contain;
  }

  #col-2-h4 {
    margin-top: 71px;
    font-family: sans-serif;
    letter-spacing: 0.1em;
    color: #047a6d;
    padding-top: 50px;
  }
  .white-bg-area {
    background-color: whitesmoke;
    width: 70%;
    margin-right: 15%;
    margin-left: 15%;
  }

  form {
    width: 60%;
    margin-left: 20%;
    margin-right: 20%;
  }

  #login-btn {
    width: 100%;
    background-color: #047a6d;
    color: #fff;
    border: transparent;
  }

  #router {
    text-decoration: none;
  }

  .linker {
    padding-bottom: 90px;
    margin-bottom: 80px;
  }

  input {
    border-left: 0px;
    border-right: 0px;
    border-top: 0px;
    border-bottom: 1px solid;
    background-color: whitesmoke;
  }
</style>



